<template>
    <div id='movies'>
        <div class="container text-left">
            <div class="back-button">
                <router-link to="/">
                    <i class="material-icons align-middle">arrow_back</i> 
                    <span>Go back</span>
                </router-link>
            </div>
            
            <!-- Show selected movie details -->
            <div class="row movie-details" v-if="selectedMovie.title != ''">
                <div class="col-sm-4">
                    <img v-bind:src="'http://image.tmdb.org/t/p/w500/' + selectedMovie.posterpath" width="100%" class="img-thumbnail">
                </div>
                <div class="col-sm-8">
                    <h4>
                        {{selectedMovie.title}}
                        <i class="material-icons align-middle delete-button" @click="destroy(selectedMovie.db_id)">delete_forever</i>
                    </h4>
                    <p>{{trimDate(selectedMovie.year)}}</p>
                    <div class="vote">
                        {{selectedMovie.vote}}<i class="material-icons star-icon">star</i>
                    </div>
                    <a target="_blank" v-bind:href="'https://www.themoviedb.org/movie/' + selectedMovie.id" class="details-btn">
                    Details
                    </a>
                </div>
            </div>

            <hr>

            <h3>Watchlist</h3>
            <!-- Show user movies -->
            <div class="row">
                <div class="col-sm-12">
                    <div class="row">
                        <div v-for="movie in movies" :key="movie.id" class="col-lg-2 col-md-3 col-sm-4 col-6 popular-result">
                            <div>
                                <img v-bind:src="'http://image.tmdb.org/t/p/w500/' + movie.posterpath" width="150px" class="img-thumbnail" v-bind:title="movie.title" v-bind:id="movie.id" v-bind:dbid="movie.db_id" v-bind:vote="movie.vote" v-bind:year="movie.year" v-bind:posterpath="movie.posterpath" v-on:click="displaySelected">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import db from '@/components/firebase'

export default {
  name: 'movies',
  data() {
      return {
          movies: [],
          selectedMovie: {
              title: '',
              vote: '',
              year: '',
              posterpath: '',
              id: '',
              db_id: ''
          }
      }
  },
  created() {
      db.settings({ timestampsInSnapshots: true })
      db.collection('movies').orderBy('movie_vote').get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
              const data = {
                'id': doc.data().movie_id,
                'title': doc.data().movie_title,
                'year': doc.data().movie_year,
                'vote': doc.data().movie_vote,
                'posterpath': doc.data().movie_poster_path,
                'db_id': doc.id
              }
              this.movies.push(data)
          })
      })

  },
  methods: {
    destroy(id) {
        db.collection('movies').doc(id).delete()
        this.$router.push('/')
    },
    trimDate(date) {
        return date.substring(0,4)
    },
    displaySelected(e) {
        this.selectedMovie.title = e.target.getAttribute('title')
        this.selectedMovie.posterpath = e.target.getAttribute('posterpath')
        this.selectedMovie.year = e.target.getAttribute('year')
        this.selectedMovie.vote = e.target.getAttribute('vote')
        this.selectedMovie.id = e.target.getAttribute('id')
        this.selectedMovie.db_id = e.target.getAttribute('dbid')
    }
  }
}
</script>


